.About_sec {
  height: 100vh;
  color: #000;
  background: #fff;
  padding: 10% 20%;
  box-sizing: border-box;
  align-items: center;
}
@media (max-width: 830px) {
  .About_sec {
    height: auto;
    padding: 20% 5%;
    align-items: center;
    justify-content: center;
  }
  .img-fluid {
    height: auto;
  }
}
