.MuiInputBase-input {
  font: inherit;
  color: currentColor;
  width: 100%;
  border: 0;
  height: 1.1876em;
  margin: 0;
  display: block;
  padding: 0 0 7px !important;
  min-width: 0;
  background: none;
  box-sizing: content-box;
  animation-name: mui-auto-fill-cancel;
  letter-spacing: inherit;
  animation-duration: 10ms;
  -webkit-tap-highlight-color: transparent;
}

.Contact {
  height: 100vh;
  color: #000;
  background: #fff;
  padding: 10% 20%;
  box-sizing: border-box;
  align-items: center;
}
@media (max-width: 768px) {
  .Contact {
    height: auto;
    padding: 20% 5%;
    align-items: center;
    justify-content: center;
  }
}
