.banner {
  height: 100vh;
  position: relative;
  background: url("../Images/C0155T01.JPG");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}
@media (max-width: 760px) {
  .banner {
    height: 100vh;

    background-repeat: no-repeat;
    background-position: 20% 60%;
    background-size: cover;
    background-attachment: fixed;
  }
}
.banner:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 500px;
  background: linear-gradient(#000, transparent);
  pointer-events: none;
}
.banner:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background: linear-gradient(360deg, #000, transparent);
  pointer-events: none;
}
.Gridbox {
  text-align: center;
  padding: 10px;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}
